import { useRef } from "react";

import Navbar from "./Navbar";
import MobileNav from "./MobileNav";
import Hero from "./Hero";
import About from "./About";
import TechStack from "./TechStack";
import Projets from "./Projects";
import Education from "./Education";
import Contact from "./Contact";

function Main() {
  const heroRef = useRef(null);
  const aboutRef = useRef(null);
  const techStackRef = useRef(null);
  const projectsRef = useRef(null);
  const educationRef = useRef(null);
  const contactRef = useRef(null);

  return (
    <div>
      {/* navbar */}
      <Navbar
        heroRef={heroRef}
        aboutRef={aboutRef}
        techStackRef={techStackRef}
        projectsRef={projectsRef}
        educationRef={educationRef}
        contactRef={contactRef}
      />

      {/* mobile nav */}
      <MobileNav
        heroRef={heroRef}
        aboutRef={aboutRef}
        techStackRef={techStackRef}
        projectsRef={projectsRef}
        educationRef={educationRef}
        contactRef={contactRef}
      />

      {/* hero section */}
      <Hero heroRef={heroRef} aboutRef={aboutRef} contactRef={contactRef} />

      {/* about me */}
      <About aboutRef={aboutRef} techStackRef={techStackRef} />

      {/* tech stack */}
      <TechStack techStackRef={techStackRef} />

      {/* projects */}
      <Projets projectsRef={projectsRef} />

      {/* education */}
      <Education educationRef={educationRef} />

      {/* contact */}
      <Contact contactRef={contactRef} />
    </div>
  );
}

export default Main;
