import { useEffect } from "react";
import Aos from "aos";
import "aos/dist/aos.css";
import { frontEnd, backEnd, general } from "../Data/icons";
import parse from "html-react-parser";

function TechStack({ techStackRef }) {
  useEffect(() => {
    Aos.init({ duration: 1500 });
  }, []);

  return (
    <section
      ref={techStackRef}
      id="techstack"
      data-aos="fade-up"
      data-aos-offset="200"
      className="p-4 text-center md:py-10 lg:py-16 dark:text-white 2xl:justify-items-center md:mx-auto"
    >
      <h1 className="text-zinc-700 dark:text-white text-4xl md:text-3xl lg:text-4xl 2xl:text-5xl 3xl:text-6xl tracking-wide font-bold my-4">
        My Tech Stack
      </h1>

      <div className="my-4 mt-20">
        <h2 className="text-2xl 2xl:text-3xl 3xl:text-4xl font-[500] dark:font-[400]">
          Front-End
        </h2>

        <div className="lg:flex lg:items-center lg:justify-center grid grid-cols-2 md:grid-cols-4 justify-items-center my-16">
          {frontEnd.map((tech, i) => (
            <div
              key={tech.name}
              className="bg-white shadow-[0px_0px_4px_rgba(0,0,0,0.25)] dark:bg-[#e5e5e51a] w-28 lg:mx-4 xl:mx-6 2xl:mx-8 3xl:mx-10 h-28 rounded-lg my-4 lg:my-0 text-center flex flex-col justify-evenly items-center"
            >
              {parse(tech.svg)}
              <p className="text-sm text-[#304565] dark:text-white font-bold tracking-wider">
                {tech.name}
              </p>
            </div>
          ))}
        </div>
      </div>

      <div className="my-4">
        <h2 className="text-2xl 2xl:text-3xl 3xl:text-4xl font-[500] dark:font-[400]">
          Back-End
        </h2>

        <div className="lg:flex lg:items-center lg:justify-center grid grid-cols-2 md:grid-cols-4 justify-items-center my-16">
          {backEnd.map((tech, i) => (
            <div
              key={tech.name}
              className="bg-white shadow-[0px_0px_4px_rgba(0,0,0,0.25)] dark:bg-[#e5e5e51a] w-28 lg:mx-6 2xl:mx-8 3xl:mx-10 h-28 rounded-lg my-4 text-center flex flex-col justify-evenly items-center"
            >
              {parse(tech.svg)}
              <p className="text-sm text-[#304565] dark:text-white font-bold tracking-wider">
                {tech.name}
              </p>
            </div>
          ))}
        </div>
      </div>

      <div className="my-4">
        <h2 className="text-2xl 2xl:text-3xl 3xl:text-4xl font-[500] dark:font-[400]">
          General
        </h2>

        <div className="lg:flex lg:items-center lg:justify-center grid grid-cols-2 md:grid-cols-4 justify-items-center my-16">
          {general.map((tech, i) => (
            <div
              key={tech.name}
              className="bg-white shadow-[0px_0px_4px_rgba(0,0,0,0.25)] dark:bg-[#e5e5e51a] w-28 lg:mx-6 2xl:mx-8 3xl:mx-10 h-28 rounded-lg my-4 text-center flex flex-col justify-evenly items-center"
            >
              {parse(tech.svg)}
              <p className="text-sm text-[#304565] dark:text-white font-bold tracking-wider">
                {tech.name}
              </p>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
}

export default TechStack;
