import { useNavigate } from "react-router-dom";
import noPage from "../images/404.svg";

function NotFound() {
  const navigate = useNavigate();

  return (
    <main className="w-full h-full App flex flex-col lg:flex-row lg:items-center lg:justify-evenly overflow-auto">
      <img src={noPage} alt="404 Page not found" className="h-screen" />

      <div>
        <h1 className="md:text-2xl 2xl:text-4xl 3xl:text-5xl my-2 2xl:my-3 3xl:my-4 dark:text-white">
          Sorry, the page you are looking for does not exist.
        </h1>

        <button
          onClick={() => navigate("/")}
          className="p-3 text-white px-10 lg:px-12 text-sm md:text-base 2xl:text-base 3xl:text-lg my-5 inline-block bg-button rounded-lg font-bold tracking-wide hover:scale-105 transition-all disabled:bg-gray-600 disabled:cursor-not-allowed disabled:hover:transform-none"
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            fill="currentColor"
            className="inline w-5 2xl:w-6 3xl:w-7 2xl:align-text-top mr-2"
            viewBox="0 0 16 16"
          >
            <path
              fill-rule="evenodd"
              d="M6 12.5a.5.5 0 0 0 .5.5h8a.5.5 0 0 0 .5-.5v-9a.5.5 0 0 0-.5-.5h-8a.5.5 0 0 0-.5.5v2a.5.5 0 0 1-1 0v-2A1.5 1.5 0 0 1 6.5 2h8A1.5 1.5 0 0 1 16 3.5v9a1.5 1.5 0 0 1-1.5 1.5h-8A1.5 1.5 0 0 1 5 12.5v-2a.5.5 0 0 1 1 0v2z"
            />
            <path
              fill-rule="evenodd"
              d="M.146 8.354a.5.5 0 0 1 0-.708l3-3a.5.5 0 1 1 .708.708L1.707 7.5H10.5a.5.5 0 0 1 0 1H1.707l2.147 2.146a.5.5 0 0 1-.708.708l-3-3z"
            />
          </svg>
          Go Back
        </button>
      </div>
    </main>
  );
}

export default NotFound;
